<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="banben">
           <span><img src="../assets/images/logo.png"></span>
           <span>{{$t('mycenter.banben')}}{{version}}</span>
           <p v-html="content.ContentInfo"></p>
        </div>
        <yd-cell-group>
            <yd-cell-item arrow type="link" href="/help_about?id=2">
                <span slot="left">{{$t('mycenter.xieyi')}}</span>
            </yd-cell-item>
            <yd-cell-item arrow type="link" href="/help_about?id=3">
        		<span slot="left">{{$t('mycenter.tioakuan')}}</span>
            </yd-cell-item>
            <!-- <yd-cell-item arrow type="link" href="/help_about?id=13">
                <span slot="left">{{$t('mycenter.rizhi')}}</span>
            </yd-cell-item> -->
            <yd-cell-item arrow type="link" href="/help_about?id=4">
                <span slot="left">{{$t('mycenter.xiangdao')}}</span>
            </yd-cell-item>
        </yd-cell-group>
    </div>
</template>

<script>
import headerBar from "./header.vue";
export default {
  components: {
    headerBar: headerBar
  },
  data() {
    return {
      headerNavData: {
        left: true,
        title: this.$t("mycenter.about"),
        right: false,
        bj: "#2c94ed",
        search: false,
        share: false
      },
      content: "",
      version: this.appVersion
    };
  },
  created() {
    this.about();
  },
  methods: {
    // 关于我们
    about() {
      var that = this;
      this.axios({
        url: this.Url + "SystemContent.ashx",
        method: "post",
        params: { req: 5, id: 1 }
      })
        .then(res => {
          if (res.data.success) {
            this.content = res.data.data;
          }
        })
        .catch(err => {});
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong {
  padding-top: 0.9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC",
    "Helvetica Neue", STHeiti, "Microsoft Yahei", Arial, Tahoma, Simsun,
    sans-serif;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.yd-cell-item {
  border-bottom: 1px solid #f4f4f4;
}
.yd-cell-item span:nth-child(2) {
  font-size: 0.3rem;
  color: #333;
}
.banben {
  padding: 0.8rem 0.5rem;
  text-align: center;
}
.banben span:nth-child(1) {
  display: block;
  width: 2.5rem;
  /* height: 1rem; */
  /*background: #ff6767;*/
  margin: 0 auto 0.3rem;
}
.banben span:nth-child(1) img {
  width: 100%;
}
.banben span:nth-child(2) {
  font-size: 0.26rem;
  color: #666;
}
.banben p {
  text-align: justify;
  line-height: 1.5;
  padding-top: 0.5rem;
  font-size: 0.28rem;
}
</style>



